import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Input, Form, Select, Button, Table, message ,Modal } from 'jdcloudui';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { selectGoodsSearch,saveFormData } from '../SelectGoodsSearch/redux';
import { insertShopSupplyItemInfo } from './redux';
import  BaseComponent  from '../../Common/BaseComponent';
import './styles/css.css';
import {getFuncPermission} from 'jdcloudecc/reducer/functionPermissions';
import {FuncPermission}  from 'jdcloudecc/components';

var selectRow = [];
@connect(
  state => ({SelectGoodsSearch:state.selectGoodsSearch,InsertShopSupplyItemInfo:state.insertShopSupplyItemInfo,funcPermissions:state.funcPermissions}),
  dispatch => bindActionCreators({selectGoodsSearch,insertShopSupplyItemInfo,saveFormData,getFuncPermission}, dispatch)
)
export default class SelectGoodsList extends BaseComponent {

  constructor(props, context) {
    super(props, context);
    this.supplyShopId=null;

    this.showConfirm = this.showConfirm.bind(this);
  }
  //校验
  static propTypes = {
    SelectGoodsSearch: PropTypes.object.isRequired,
    InsertShopSupplyItemInfo: PropTypes.object.isRequired,
    selectGoodsSearch: PropTypes.func.isRequired,
    insertShopSupplyItemInfo: PropTypes.func.isRequired,
    saveFormData: PropTypes.func.isRequired
  };

  //获取初始列表数据
  componentWillMount() {
    var initialValue={platformId:this.platformId,userId:1,pageSize:this.props.pageSize,pageNum:1};
    this.props.saveFormData(initialValue);
    this.props.selectGoodsSearch(initialValue).then(
      (result)=>{console.log('SearchData success')},
      (error)=>{console.log('SearchData fail')}
    );
    this.props.getFuncPermission();
  }

  //生成表头
  createColumns(){
    const columns = [{
      title: '商品名称',
      dataIndex: 'name',
      key: 'name',
      width: '60%'
    }, {
      title: '商品分类',
      dataIndex: 'categorys',
      key: 'categorys',
      width: '30%'
    }, {
      title: '操作',
      key: 'action',
      width: '10%',
      render: (record) => (
        <span>
          <FuncPermission codes={this.codesResponse} code="submit">
            <a onClick={()=>{this.onInsert(record)}}>提交</a>
          </FuncPermission>
        </span>
      ),
    }];
    return columns;
  }

  //生成列表数据
  createData() {
    var dataSource = new Array();
    this.props.SelectGoodsSearch.data && this.props.SelectGoodsSearch.data.data && this.props.SelectGoodsSearch.data.data.result.map((good)=>{

      //拼装类目
      var categorys = (good.firstLevName===null?'':good.firstLevName)
        +(good.sendLevName===null?'':'--'+good.sendLevName)
        +(good.thirdLevName===null?'':'--'+good.thirdLevName)
        +(good.fourthLevName===null?'':'--'+good.fourthLevName);

      //生成数据
      var data = {
        key: good.itemId,
        name: <div className="f-pr pl-60"><img src={good.pictureUrl} width='60' height='60' className="f-fl imgBox" /><span className="f-db">{good.itemName}</span></div>,
        categorys: categorys,
        supplyShopId:good.supplyShopId
      };
      dataSource.push(data);
      this.supplyShopId =good.supplyShopId;
    });
    return dataSource;
  }

  //单个提交
  onInsert(record){
    debugger;
    var itemIds=new Array();
    itemIds.push(record.key);
    this.props.insertShopSupplyItemInfo({
      platformId:this.platformId,
      supplySellerId:4,
      itemIds:itemIds,
      supplyShopId:record.supplyShopId,
    }).then(
      (result)=>{ this.showConfirm() },
      (error)=>{ debugger; message.warning(result.msg);this.refreshList();}
    );
  }

  //批量提交
  onInsertAll() {
    if(selectRow.length<=0){
      message.info('请选择商品！');
      return;
    }
    debugger;
    this.props.insertShopSupplyItemInfo({
      platformId:this.platformId,
      supplySellerId:4,
      itemIds:selectRow,
      supplyShopId:this.supplyShopId
    }).then(
      (result)=>{this.showConfirm();},
      (error)=>{message.info(result.msg);this.refreshList();}
    );
  }

  showConfirm() {
    let tmp = this;
    Modal.confirm({
      title: '操作提示',
      content: '您的申请已记录，是否提交供货信息？',
      okText: '继续选择',
      cancelText: '确定',
      onOk() {
        //刷新页面
        tmp.refreshList();
      },
      onCancel() {
        //跳转商品信息管理页
        window.location='/item-shop-view/iteminfo';
      },
    });
  }

  //分页查询
  onChangePage(pageNumber){
    debugger;
    console.log("page change:",pageNumber);
    var searchData = this.props.SelectGoodsSearch.searchdata.searchdata;
    var pageChange ={...searchData,pageNum:pageNumber};
    this.props.selectGoodsSearch(pageChange).then(
      (result)=>{console.log('PageChange SearchData success')},
      (error)=>{console.log('PageChange SearchData fail')}
    );
  }

  //生成分页对象
  createPagination(){
    var total = this.props.SelectGoodsSearch.data && this.props.SelectGoodsSearch.data.data && this.props.SelectGoodsSearch.data.data.totalCount;
    var current = this.props.SelectGoodsSearch.data && this.props.SelectGoodsSearch.data.data && this.props.SelectGoodsSearch.data.data.pageNum;

    return {
      showQuickJumper:true,
      total:total,
      current:current,
      pageSize:this.props.pageSize,
      onChange:(pageNumber)=>this.onChangePage(pageNumber)
    };
  }

  //生成选中事件对象
  createRowSelection(){
    return{
      onChange: (selectedRowKeys, selectedRows)=>this.onChangeRow(selectedRowKeys, selectedRows),
      onSelect: (record, selected, selectedRows) =>this.onSelectRow(record, selected, selectedRows),
      onSelectAll: (selected, selectedRows, changeRows) =>this.onSelectAllRow(selected, selectedRows, changeRows)};
  }

  //checkBox响应事件
  onChangeRow(selectedRowKeys, selectedRows) {
    selectRow = selectedRowKeys;
  }
  onSelectRow(record, selected, selectedRows) {
    console.log(record, selected, selectedRows);
  }
  onSelectAllRow(selected, selectedRows, changeRows){
    console.log(selected, selectedRows, changeRows);
  }

  //刷新页面(获取搜索参数，重新查询)
  refreshList(){
    var searchValue = this.props.SelectGoodsSearch.searchdata.searchdata;
    this.props.selectGoodsSearch(searchValue);
  }

  render() {
    //菜单权限数据处理
    let permissionData = this.props.funcPermissions.data;
    if(permissionData && permissionData.code != 0){
      message.warning("菜单权限获取失败！");
    }
    if(permissionData && permissionData.code == 0){
      this.codesResponse = permissionData.data;
      this.codes = this.codesResponse && this.codesResponse.join(",");
    }
    return(
      <div>
        <div className="tbl-top-action">
          <FuncPermission codes={this.codesResponse} code="batchSubmit">
            <Button type="primary" onClick={()=>this.onInsertAll()}>提交</Button>
          </FuncPermission>
        </div>
        <div className="tableBorderSelect">
          <Table
            rowSelection={true && this.createRowSelection()}
            columns={true && this.createColumns()}
            dataSource={true && this.createData()}
            pagination={true && this.createPagination()}
            loading={this.props.SelectGoodsSearch.loading}
          />
        </div>
      </div>
    )
  }
}

